.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.PageHeader {
  background-color: #757575;
  height: 5rem;
  width: 100%;
}

.row {
  margin-left: 1rem;
}

.sideBar {
  /* background-color: #F6F6F6; */
  height: 65rem;
  overflow-y: auto;
}

.chatView {
  /* background-color:gold; */
  width: 73%;
  height: 65rem;
}

.channelsMenu {
  color: #757575;
}

.directMessagesMenu {
  color: #757575;
}

.NameTags {
  margin-top: 1rem;
  margin-bottom: 1rem;
  background-color: #FFFFFF;
  height: 5rem;
}

h4 {
  margin-left: 1rem;
}

.UserName {
  color: #B7B7B7;
  margin-left: 2rem;
  margin-top: 1.25rem;
  font-size: 2rem;
  font-weight: normal;
}

/* ::-webkit-scrollbar {
    display: none;
  } */

/* width */
::-webkit-scrollbar {
  width: 25px;
  color: #FFFFFF;
  border-color: #707070;
  background-color: #757575
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: 0 0 0 0.1rem #707070;
  border-radius: 20px;
  background-color: #FFFFFF;
  margin-top: 4rem;
  /* margin-bottom: 1rem; */
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #B8B8B8;
  width: 20px;
  border-radius: 20px;
  border: 5px solid #FFFFFF;
  /* border-color: #707070; */
}

.home {
  background-image: url(/imgs/SPINIFYIMAGE.jpg), linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
  background-blend-mode: overlay;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
}

.main {
  margin-left: 218px;
  margin-right: 217px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 2rem;
}

#header {
  color: white;
  font-size: 4vw;
  text-align: center;
  font-weight: bold;
}

#joinbtn {
  text-transform: uppercase;
  background-color: #FFEB3B;
  width: 20vw;
  height: 6vw;
  font-size: 2vw;
  font-weight: bolder;
  border-radius: 12px;
  margin-top: 1rem;
  margin-bottom: 5rem;
  text-align: center;
}

#signinbtn {
  width: 12vw;
  height: 4vw;
  font-size: 1.5vw;
  font-weight: bold;
  border-radius: 7px;
  text-align: center;
  background-color: white;
  margin-bottom: 2rem;
}

.break {
  height: 1rem;
}

/*For WorkSpace*/
:root {
  --custom--grey: #757575;
  --custom-light-grey: #F6F6F6;
  --custome-yellow: #FFEB3B
}

.class-sticky {
  position: sticky;
  top: 0;
}

#workspace-header {
  padding: 1rem;
  background-color: var(--custom--grey);
  color: var(--custome-yellow);
  font-family: "Gill Sans", "Gill Sans MT", Calibri, sans-serif;
}

#workspace-header-details {
  display: flex;
  flex-direction: row;
}

#workspace-header-img {
  height: 40px;
  width: auto;
}

#workspace-header-title {
  font-size: 1.5rem;
  font-weight: bold;
  margin: 0px;
  margin-left: 0.8rem;

}

#workspace-header-img {
  background-color: white;
  border: 1px white solid;
  border-radius: 20px;
}

#workspace-header-channeltitle {
  font-size: 1.4rem;
}

#workspace-header-icons {
  font-size: 1.4rem;
  color: white;
  display: inline-flex;
  justify-content: flex-end;
  align-items: center;
}

#workspace-header-icons svg {
  margin-left: 1rem;
}

#workspace-header-icons .fa-bars {
  font-size: 2rem;
}

#workspace-body{
  background-color: var(--custom-light-grey);
  height: 100vh;
  padding-top: 0.6rem;
  display: flex;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, sans-serif;
}

.workspace-body-title {
  color: var(--custom--grey);
  font-family: "Gill Sans", "Gill Sans MT", Calibri, sans-serif;
  font-size: 1.3rem;
  display:flex;
  padding-top: 0.6rem;
  align-items: center;
  justify-content: space-between;
}

.workspace-body-title svg{
  font-size: 1rem;
}

.workspace-body-information{
    color: var(--custom--grey);
    opacity: 0.5;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, sans-serif;
  font-size: 1.1rem;
  margin-left: auto !important;
}

#workspace-body-chat .fa-comments {
  font-size: 15rem;
  color:var(--custom--grey);
  opacity: 0.5;
  margin-top:0;
}

#loading-bg{
  width: 100vw;
  height: 100vh;
  background-color: var(--custom-light-grey);
}

.loading-img {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 25rem;
  height: 25rem;
  margin: -12rem 0 0 -12rem;
  -webkit-animation: spin 4s linear infinite;
  -moz-animation: spin 4s linear infinite;
  animation: spin 4s linear infinite;
}

@-moz-keyframes spin {
  100% {
    -moz-transform: rotate(360deg);
  }
}

@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

/*To Remove*/
#workspace-body-chat{
  margin-top:5rem;
}